<template>
  <div class="Top2" style="min-height:100vh">
    <div :style="'height:' + this.$root.pageHeight + 'px'" />
    <div id="mainArea" class="mainArea bgBlack2" :style="{ padding: '20px', backgroundColor: mainAreaBgColor }">
      <div style="max-width: 800px; margin: 0 auto;">
        <input type="number" v-model="alertValue" placeholder="„Ç¢„É©„Éº„ÉàÂÄ§" />
        <h1 style="color: #fff; text-align: center; margin-bottom: 20px;">Monitor</h1>
        <div style="background-color: #333; padding: 20px; border-radius: 8px;">
          <p style="color: #fff; font-size: 18px; margin-bottom: 10px;">„Åó„Çá„Åº„ÅÆÊúÄÂ§ß<span style="color: #ffcc00;">{{ maxShoboCreatedAt }} - {{ maxShoboSabun }}</span></p>
          <p style="color: #fff; font-size: 18px; margin-bottom: 10px;">„Åó„Çá„Åº„ÅÆÊúÄÊñ∞<span style="color: #ffcc00;">{{ firstShoboCreatedAt }} - {{ firstShoboSabun }}</span></p>
          <div class="sp_line_ss" />
          <p style="color: #fff; font-size: 18px; margin-bottom: 10px;">„Éï„Ç°„É≥Â§™ÊúÄÂ§ß<span style="color: #ffcc00;">{{ maxFantaCreatedAt }} - {{ maxFantaSabun }}</span></p>
          <p style="color: #fff; font-size: 18px; margin-bottom: 10px;">„Éï„Ç°„É≥Â§™„ÅÆÊúÄÊñ∞<span style="color: #ffcc00;">{{ firstFantaCreatedAt }} - {{ firstFantaSabun }}</span></p>
          <div class="sp_line_l" />

          <table class="bgBlack2" style="width: 100%; color: #fff; margin-top: 20px; border-collapse: collapse;">
            <thead>
              <tr>
                <th style="border: 1px solid #fff; padding: 8px;">ÊôÇÈñì</th>
                <th style="border: 1px solid #fff; padding: 8px;">„Åó„Çá„Åº„ÅÆsabun</th>
                <th style="border: 1px solid #fff; padding: 8px;">„Éï„Ç°„É≥Â§™„ÅÆsabun</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in combinedList" :key="index">
                <td style="border: 1px solid #fff; padding: 8px;">{{ item.time }}</td>
                <td style="border: 1px solid #fff; padding: 8px;">{{ item.shoboSabun }}</td>
                <td style="border: 1px solid #fff; padding: 8px;">{{ item.fantaSabun }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Monitor',
  data() {
    return {
      maxShoboCreatedAt: '',
      maxShoboSabun: 0,
      maxFantaCreatedAt: '',
      maxFantaSabun: 0,
      firstShoboCreatedAt: '',
      firstShoboSabun: 0,
      firstFantaCreatedAt: '',
      firstFantaSabun: 0,
      shoboList: [],
      fantaList: [],
      mainAreaBgColor: '#333', // „Éá„Éï„Ç©„É´„Éà„ÅÆËÉåÊôØËâ≤
      audioBlue: null,
      audioOrange: null,
      alertValue : 120,
      combinedList: []
    }
  },
  watch: {
    shoboList: {
      handler() {
        this.combineLists();
      },
      deep: true
    },
    fantaList: {
      handler() {
        this.combineLists();
      },
      deep: true
    }
  },
  created: function() {
    this.audioBlue = new Audio('/app/sounds/blue.mp3');
    this.audioBlue.volume = 0.05;
    this.audioBlue.addEventListener('error', (e) => {
      console.error('Error loading blue.mp3:', e);
    });
    this.audioOrange = new Audio('/app/sounds/orange.mp3');
    this.audioOrange.volume = 0.05;
    this.audioOrange.addEventListener('error', (e) => {
      console.error('Error loading orange.mp3:', e);
    });
  },
  mounted() {
    this.getList();
    setInterval(() => {
      this.getList();
    }, 120000);
  },
  methods: {
    combineLists() {
      const combined = [];
      const maxLength = Math.max(this.shoboList.length, this.fantaList.length);

      // sabun„ÅÆÈ†Ü‰Ωç„ÇíË®àÁÆó
      const shoboRankings = this.calculateRankings(this.shoboList, 'sabun');
      const fantaRankings = this.calculateRankings(this.fantaList, 'sabun');

      for (let i = 0; i < maxLength; i++) {
        combined.push({
          time: this.shoboList[i]?.created_at || this.fantaList[i]?.created_at,
          shoboSabun: this.formatSabunWithRank(this.shoboList[i]?.sabun, shoboRankings),
          fantaSabun: this.formatSabunWithRank(this.fantaList[i]?.sabun, fantaRankings)
        });
      }
      this.combinedList = combined;
    },
    calculateRankings(list, key) {
      const sorted = [...list].sort((a, b) => b[key] - a[key]);
      const rankings = new Map();
      sorted.slice(0, 10).forEach((item, index) => {
        rankings.set(item[key], index + 1);
      });
      return rankings;
    },
    formatSabunWithRank(sabun, rankings) {
      if (!sabun) return '';
      const rank = rankings.get(sabun);
      return rank ? `${sabun}Ôºà${rank}‰ΩçÔºâ` : sabun;
    },
    // ÁîªÈù¢ÂàùÊúüÂåñÊôÇ„ÅÆÂá¶ÁêÜ„ÇíË°å„ÅÜ„É°„ÇΩ„ÉÉ„Éâ
    initAction: function() {
      // ÂøÖË¶Å„Å´Âøú„Åò„Å¶ÂàùÊúüÂåñÂá¶ÁêÜ„ÇíÂÆüË£Ö
      this.getList();
    },
    getList: function() {
      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + "getMonitorInfo", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);  
        // console.log(sukiList);
        let shoboList = resData["shoboList"];
        let fantaList = resData["fantaList"];

        if (shoboList.length == 0 || fantaList.length == 0) {
          return;
        }

        //„Éá„Éº„Çø„ÇíË™øÊï¥ Ë£úÂÆå
        for (let i = 0; i < shoboList.length; i++) {
          const element = shoboList[i];
          let dateStr = element.created_at;
          let date = new Date(dateStr);
          element.created_at = date.getFullYear() + "/" + (date.getMonth() + 1) + "/" + date.getDate() + " " + date.getHours() + ":" + date.getMinutes();
        }
        //„Éá„Éº„Çø„ÇíË™øÊï¥ Ë£úÂÆå
        for (let i = 0; i < fantaList.length; i++) {
          const element = fantaList[i];
          let dateStr = element.created_at;
          let date = new Date(dateStr);
          element.created_at = date.getFullYear() + "/" + (date.getMonth() + 1) + "/" + date.getDate() + " " + date.getHours() + ":" + date.getMinutes();
        }
        
        // ËøΩÂä†„Åó„Åü„Ç≥„Éº„Éâ
        shoboList.forEach((item, index) => {
          if (index < shoboList.length - 1) {
            item.sabun = item.no - shoboList[index + 1].no;
          } else {
            item.sabun = 0; // ÊúÄÂæå„ÅÆË¶ÅÁ¥†„ÅØÂ∑ÆÂàÜ„Çí0„Å®„Åô„Çã
          }
        });
        
        fantaList.forEach((item, index) => {
          if (index < fantaList.length - 1) {
            item.sabun = item.no - fantaList[index + 1].no;
          } else {
            item.sabun = 0; // ÊúÄÂæå„ÅÆË¶ÅÁ¥†„ÅØÂ∑ÆÂàÜ„Çí0„Å®„Åô„Çã
          }
        });
        
        // ËøΩÂä†„Åó„Åü„Ç≥„Éº„Éâ
        let maxShobo = shoboList.reduce((max, current) => max.sabun > current.sabun ? max : current);
        let maxFanta = fantaList.reduce((max, current) => max.sabun > current.sabun ? max : current);
        
        this.maxShoboCreatedAt = maxShobo.created_at;
        this.maxShoboSabun = maxShobo.sabun;
        this.maxFantaCreatedAt = maxFanta.created_at;
        this.maxFantaSabun = maxFanta.sabun;
        
        this.firstShoboCreatedAt = shoboList[0].created_at;
        this.firstShoboSabun = shoboList[0].sabun;
        this.firstFantaCreatedAt = fantaList[0].created_at;
        this.firstFantaSabun = fantaList[0].sabun;
        
        this.shoboList = shoboList;
        this.fantaList = fantaList;
        
        // ËÉåÊôØËâ≤„ÇíÊù°‰ª∂„Å´Âøú„Åò„Å¶Ë®≠ÂÆö
        if (this.maxShoboCreatedAt === this.firstShoboCreatedAt && this.maxShoboSabun > this.alertValue) {
          this.mainAreaBgColor = '#1a73e8'; // ÈùíÁ≥ª„ÅÆËâ≤
          this.playSound('blue');
        } else if (this.maxFantaCreatedAt === this.firstFantaCreatedAt && this.maxFantaSabun > this.alertValue) {
          this.mainAreaBgColor = '#ff6d00'; // „Ç™„É¨„É≥„Ç∏Á≥ª„ÅÆËâ≤
          this.playSound('orange');
        } else {
          this.mainAreaBgColor = '#333'; // „Éá„Éï„Ç©„É´„Éà„ÅÆËÉåÊôØËâ≤
        }
      });
    },
    playSound(type) {
      if (type === 'blue') {
        this.audioBlue.play();
      } else if (type === 'orange') {
        this.audioOrange.play();
      }
    }
  },
  // „É´„Éº„ÉàÈÅ∑ÁßªÊôÇ„Å´ÂÆüË°å„Åï„Çå„Çã„Éï„ÉÉ„ÇØ
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  // „É´„Éº„Éà„ÅåÊõ¥Êñ∞„Åï„Çå„ÇãÊôÇ„Å´ÂÆüË°å„Åï„Çå„Çã„Éï„ÉÉ„ÇØ
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  }
}
</script>

